<template>
  <div class="user-member">
    <Card class="def-box-shadow" style="height: 72px; margin-bottom: 15px">
        <Col :span="3">
            <Button icon="md-add" type="primary" size="large" @click="addUser">
                
            </Button>
        </Col>
        <Col :span="6" offset="15">
            <Input size="large"
            @on-search="()=>{queryEntity.pageNum=1;loadUserList();}"
             search 
             enter-button
              placeholder="" 
              v-model="queryEntity.likeBox.likeValue"/>
        </Col>
    </Card>
    <Card class="def-box-shadow auto_height">
      <Table
        row-key="id"
        :loading="tableModel.loading"
        :columns="tableModel.columns"
        :data="tableModel.columnDatas"
        border
      ></Table>
      <div style="text-align:right;padding:15px 0">
         <Page 
         :total="queryEntity.total" 
         size="small" 
         :page-size="queryEntity.pageSize"
         show-total 
         show-sizer  
          :current="queryEntity.pageNum"
         @on-change="changePage"
         @on-page-size-change="changePageSize"
         />
      </div>
    </Card>

    <Modal
      :footer-hide="true"
      :mask-closable="false"
      v-model="modalBox.show"
      :title="modalBox.title"
      width="500"
    >
      <Form :model="userEntity" :label-width="80" style="margin-top: 20px">
        <FormItem label="">
          <Input
            v-model="userEntity.username"
            :disabled="userEntity.id != null && userEntity != undefined"
            placeholder=""
          />
        </FormItem>

        <FormItem label="">
          <Select
            v-model="userEntity.roleId"
            filterable
            :disabled="userEntity.username && userEntity.username === 'admin'"
          >
            <Option v-for="item in roleList" :value="item.id" :key="item.id">{{
              item.roleName
            }}</Option>
          </Select>
        </FormItem>

        <FormItem label="">
          <Input
            type="email"
            v-model="userEntity.email"
            placeholder=""
          />
        </FormItem>

        <FormItem label="" v-if="!userEntity.id">
          <Input
            type="password"
            v-model="userEntity.password"
            placeholder=""
          />
        </FormItem>

        <FormItem>
          <Button
            type="primary"
            style="margin-left: 80px"
            size="large"
            @click="saveData"
            ></Button
          >
          <Button
            type="default"
            style="margin-left: 25px"
            size="large"
            @click="modalBox.show = false"
            ></Button
          >
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>

<script>
import MD5 from 'md5'
export default {
  name: "user-member",
  components: {},
  data() {
    return {
      queryEntity: {
        total: 0,
        pageNum: 1,
        pageSize: 10,
        likeBox:{
          likeValue:"",
          column:"username"
        }
      },
      tableModel: {
        loading: true,
        columns: [
          {
            title: "ID",
            key: "id",
          },
          {
            title: "",
            key: "username",
          },
          {
            title: "",
            render: (h, params) => {
              return h("Tag", this.getRoleObjById(params.row.roleId).roleName);
            },
          },
          {
            title: "",
            key: "email",
          },
          {
            title: "",
            key: "updateTime",
          },
          {
            title: "",
            width: 220,
            render: (h, params) => {
              const btn_group_array = [];

              btn_group_array.push(
                h(
                  "Button",
                  {
                    props: {
                      type: "info",
                      size: "small",
                      ghost: true,
                    },
                    on: {
                      click: () => {
                        this.updateUser(params.row);
                      },
                    },
                  },
                  ""
                )
              );

              if (
                this.getRoleObjById(params.row.roleId).roleCode !==
                "Administrator"
              ) {
                btn_group_array.push(
                  h(
                    "Button",
                    {
                      style: {
                        "margin-left": "10px",
                      },
                      props: {
                        type: "info",
                        size: "small",
                        ghost: true,
                      },
                      on: {
                        click: () => {
                          this.resetPass(params.row);
                        },
                      },
                    },
                    ""
                  )
                );

                btn_group_array.push(
                  h(
                    "Button",
                    {
                      style: {
                        "margin-left": "10px",
                      },
                      props: {
                        type: "warning",
                        size: "small",
                        ghost: true,
                      },
                      on: {
                        click:()=>{
                          this.deleteUser(params.row);
                        },
                      },
                    },
                    ""
                  )
                );
              }

              return h("div", btn_group_array);
            },
          },
        ],
        columnDatas: [],
      },
      roleList: [],
      userEntity: {},
      modalBox: {
        show: false,
        title: "",
      },
      newPass:""
    };
  },
  created() {
    this.loadRoleList(() => {
      this.loadUserList();
    });
  },
  methods: {
    changePage(currentPage){
      this.queryEntity.pageNum = currentPage;
      this.loadUserList();

    },
    changePageSize(newPageSize){
      this.queryEntity.pageSize = newPageSize;
      this.loadUserList();
    },
    /**
     * 
     */
    loadRoleList(callback) {
      this.$server.role.getRoleList().then((res) => {
        var data = res.data;
        if (data.code == 200) {
          this.roleList = data.data;
          if (callback) {
            callback();
          }
        } else {
          this.loadRoleList();
        }
      });
    },
    /**
     * 
     */
    loadUserList() {
      this.tableModel.loading = true;
      this.$server.user.queryUserList(this.queryEntity).then((res) => {
        var { data } = res;

        this.tableModel.loading = false;
        if (data.code == 200) {
          this.tableModel.columnDatas = data.data.records;
          this.queryEntity.total = data.data.total;
        } else {
          this.$Message.warning(data.msg);
        }
      });
    },
    /**
     * id
     */
    getRoleObjById(roleId) {
      for (let i = 0; i < this.roleList.length; i++) {
        const item = this.roleList[i];
        if (item.id == roleId) {
          return item;
        }
      }
      return "";
    },
    // 
    updateUser(row) {
      this.userEntity = Object.assign({}, row);
      this.modalBox = {
        show: true,
        title: "",
      };
    },

    // 
    deleteUser(row) {
        this.$Modal.confirm({
                title: "",
                content:
                `<p>[${row.username}]?</p>`,
                loading: true,
                onOk: () => {
                this.$server.user.deleteUser(row.id).then((res) => {
                    const data = res.data;
                   this.$Modal.remove();

                    if (data.code == 200) {

                      this.loadUserList();
                      this.$Message.info('')

                    } else {
                    this.$Message.warning({
                        content: data.msg,
                        duration: 2,
                    });
                    }
                });
                },
            });

    },

    // 
    addUser() {
      this.userEntity = {};
      this.modalBox = {
        show: true,
        title: "",
      };
    },
    // 
    saveData() {
      const entity = this.userEntity;
      if (!entity.username || entity.username.trim() == "") {
        this.$Message.warning("");
        return;
      }

      if (!entity.roleId) {
        this.$Message.warning("");
        return;
      }

      if (!entity.id) {
        // id 

        if (!entity.password || entity.password.trim() == "") {
          this.$Message.warning("");
          return;
        }

        entity.password=MD5(entity.password)

        this.$server.user.createUser(entity).then((res) => {
          const { data } = res;
          if (data.code == 200) {
            this.modalBox.show = false;
            this.loadUserList();
            this.$Message.success("");
          } else {
            this.$Message.warning(data.msg);
          }
        });
      } else {
        // id 
        this.$server.user.updateUser(entity).then((res) => {
          const { data } = res;

          if (data.code == 200) {
            this.modalBox.show = false;
            this.loadUserList();
            this.$Message.success("");
          } else {
            this.$Message.warning(data.msg);
          }
        });
      }
    },
    // 
    resetPass(row) {
      this.newPass = "";
      this.$Modal.confirm({
        title: "",
        loading:true,
        render: (h) => {
          const elements = [];
          elements.push(
            h("p", [
              ``,
              h("b", { style: { color: "red" } }, `${row.username}`),
              ",",
            ])
          );

          elements.push(
            h("p",{
                style:{
                    'margin-top':'15px'
                }
            }, [
                h('Input',{
                    props:{
                        value:this.newPass,
                        placeholder:""
                    },
                    on:{
                        input:(val)=>{
                            this.newPass = val;
                        }
                    }
                })
            ])
          );


          return h("div", elements);
        },
        onOk:()=>{


            const updateData = {
                id:row.id,
                password:MD5(this.newPass)
            }

            this.$server.user.resetPass(updateData).then(res=>{
                const data = res.data;
                    this.$Modal.remove();

                 if (data.code == 200) {
                    this.$Message.success("");
                } else {
                    this.$Message.warning(data.msg);
                }
                
            })
      
             
        }
      });

    },
  },
};
</script>

<style lang="scss" scoped>
.user-member {
  height: calc(100vh - 50px);
  overflow-y: auto;
  position: relative;
  padding: 15px;
}

.auto_height {
  height: calc(100% - 85px);
  min-height: 500px;
  overflow-y:auto;
  overflow-x:hidden;

  & > * {
    height: inherit;
  }

}
</style>